import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";

const PerformanceAuthor = () => {
  const textRef = useRef(null);

  useEffect(() => {
    const text = textRef.current;

    if (text) {
      text.innerHTML = text.innerText
        .split("")
        .map(
          (char, i) =>
            `<span style="transform:rotate(${i * 11.5}deg)">${char}</span>`
        )
        .join("");
    }
  }, []);

  const [stats, setStats] = useState([]);

  const fetchStats = async () => {
    const res = await axios.get(
      `${process.env.REACT_APP_BACKEND_URL}/api/admin/stats`
    );
    if (res.status === 200)
       setStats(res.data);
  };



  useEffect(() => {
    fetchStats();
  }, []);

  return (
    <section className="top-performance overflow-hidden padding-y-60 position-relative z-index-1">
      {/* <img
        src="assets/images/shapes/spider-net.png"
        alt=""
        className="spider-net position-absolute top-0 end-0 z-index--1 white-version"
      />

      <img
        src="assets/images/shapes/spider-net-white.png"
        alt=""
        className="spider-net position-absolute top-0 end-0 z-index--1 dark-version"
      /> */}

      {/* <img
        src="assets/images/shapes/pattern-curve-four.png"
        alt=""
        className="position-absolute top-0 start-0 z-index--1"
      /> */}

      {/* <img
        src="assets/images/shapes/element2.png"
        alt=""
        className="element two"
      /> */}

      <div className="container container-two">
        <div className="row gy-4 align-items-center flex-wrap-reverse">
          <div className="col-lg-7 pe-lg-5">
            <div className="position-relative">
              <div className="circle style-two static-circle">
                <div className="circle__badge">
                  <img src="assets/images/icons/featured-badge.png" alt="" />
                </div>

                <div className="circle__desc circle__text">
                  <p ref={textRef}>4ceps 4ceps 4ceps 4ceps 4ceps</p>
                </div>
              </div>

              <div className="performance-content">
                <div className="performance-content__item">
                  <span className="performance-content__text font-18">
                    Verified Healthcare Centres
                  </span>

                  <h4 className="performance-content__count">{stats.totalCentres}</h4>
                </div>

                <div className="performance-content__item">
                  <span className="performance-content__text font-18">
                     Health Checkup Packages
                  </span>

                  <h4 className="performance-content__count">{stats.totalPackages}</h4>
                </div>

                <div className="performance-content__item">
                  <span className="performance-content__text font-18">
                    Healthcare Services
                  </span>

                  <h4 className="performance-content__count">{stats.totalServices}</h4>
                </div>

                <div className="performance-content__item">
                  <span className="performance-content__text font-18">
                    Doctors
                  </span>

                  <h4 className="performance-content__count">{stats.totalDoctors}</h4>
                </div>
              </div>
            </div>
          </div>

          <div className="col-lg-5">
            <div className="section-content">
              <div className="section-heading style-left">
                <h3 className="section-heading__title">Why People Trust Us</h3>
                <p className="section-heading__desc font-18 w-sm">
                  With verified healthcare providers, easy appointment booking,
                  and 24/7 support, we make access to quality care simple and
                  stress-free. Trusted by thousands of patients across cities
                  every day.
                </p>
              </div>
              <Link
                to="/user-register"
                className="btn btn-main btn-lg pill fw-300"
              >
                Register
              </Link>
            </div>
          </div>
        </div>
        
      </div>
     <style>
{`
  @media (max-width: 426px) {
    .performance-content__text {
      padding-left: 10px;
      display: block; /* ensure wrapping */
    }

    .performance-content__count {
      font-size: 18px; /* shrink numbers */
    }

    .performance-content {
      display: flex;
      flex-wrap: wrap; /* force wrapping */
      gap: 10px;
    }

    .performance-content__item {
      flex: 1 1 45%; /* make each item fit within small screen */
      text-align: center;
    }
  }
`}
</style>

    </section>
  );
};


export default PerformanceAuthor;
