import { Link } from "react-router-dom";

const PopularOne = () => {
  const categories = [
    {
      label: "Doctor Consultation",
      image: "/assets/images/categories/Webp/OPD-Consultations1.webp",
    },
    {
      label: "Vaccination Services",
      image: "/assets/images/categories/Webp/Vaccinations1.webp",
    },
    {
      label: "Lab and Diagnostic",
      image: "/assets/images/categories/Webp/Diagnostics1.webp",
    },
    {
      label: "Operative Procedures",
      image: "/assets/images/categories/Webp/Operative-Procedures1.webp",
    },
    {
      label: "Health Checkup Packages",
      image: "/assets/images/categories/Webp/Health-Check1.webp",
    },
      {
      label: "Non Surgical",
      image: "/assets/images/categories/Webp/Non-surgical1.webp",
    },
  ];

  return (
    <section className="popular padding-y-60 overflow-hidden">
      <div className="container container-two">
        <div className="section-heading mb-64">
          <h5
            className="section-heading__title"
            style={{ fontFamily: "inter" }}
          >
            Top Healthcare Service Categories
          </h5>
        </div>

        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            padding: "0px 20px",
          }}
        >
          <div className="popular_Cat"
            style={{
              display: "flex",
              gap: "20px",
              maxWidth: "1400px",
              flexWrap: "nowrap",
              justifyContent: "center",
            }}
          >
            {categories.map((item, index) => (
              <Link
                key={index}
                to={
                  item.label === "Health Checkup Packages"
                    ? "/all-package"
                    : `/all-service?category=${encodeURIComponent(item.label)}`
                }
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  width: "200px",
                  textAlign: "center",
                  textDecoration: "none",
                  color: "inherit",
                }}
              >
                <img  className="popular_img"
                  src={item.image}
                  alt={item.label}
                  style={{
                    width: "100%",
                    height: "150px",
                    objectFit: "cover",
                    borderRadius: "8px",
                  }}
                />
                <span className="popular_label"
                  style={{
                    marginTop: "10px",
                    fontWeight: "500",
                    fontSize: "16px",
                  }}
                >
                  {item.label}
                </span>
              </Link>
            ))}
          </div>
        </div>

        <div className="popular__button text-center">
          <Link
            to="/all-service"
            className="font-18 fw-600 text-heading hover-text-main text-decoration-underline font-heading"
            style={{ fontFamily: "inter" }}
          >
            Explore More
          </Link>
        </div>
      </div>
      <style>
        {
          `@media(max-width:1280px){
            .popular_Cat{
             flex-wrap: wrap !important;
            justify-content: center !important;
            }
          }
              @media (max-width: 483px) {
            .popular_Cat {
              gap: 10px !important;
            }
            
            .popular_img {
              height: 130px !important;
              width: 150px !important;
            }
            
            .popular_label {
              font-size: 12px !important;
            }
            
            /* Force 2 items per row */
            .popular_Cat a {
              width: calc(50% - 10px) !important;
              max-width: 150px !important;
            }
          }

          /* Optional: For very small screens below 350px */
          @media (max-width: 350px) {
            .popular_Cat a {
              width: calc(50% - 5px) !important;
              max-width: 140px !important;
            }
            
            .popular_img {
              height: 120px !important;
              width: 140px !important;
            }
          }
          `
        }
      </style>
    </section>
  );
};

export default PopularOne;
