import { Link } from "react-router-dom";
import Slider from "react-slick";

const SellingOne = () => {
  function SampleNextArrow(props) {
    const { className, onClick } = props;
    return (
      <button className={className} onClick={onClick}>
        <i className="fas fa-arrow-right" />
      </button>
    );
  }
  function SamplePrevArrow(props) {
    const { className, onClick } = props;

    return (
      <button className={className} onClick={onClick}>
        <i className="fas fa-arrow-left" />
      </button>
    );
  }
  const settings = {
    dots: false,
    arrows: true,
    infinite: true,
    speed: 1000,
    slidesToShow: 4,
    slidesToScroll: 1,
    initialSlide: 0,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <section className="selling-product padding-y-60 position-relative z-index-1 overflow-hidden">
      <img
        src="assets/images/gradients/selling-gradient.webp"
        alt=""
        className="bg--gradient"
      />

      {/* <img
				src="assets/images/shapes/element2.png"
				alt=""
				className="element one"
			/>

			<img
				src="assets/images/shapes/element1.png"
				alt=""
				className="element two"
			/> */}

      <img
        src="assets/images/shapes/curve-pattern1.png"
        alt=""
        className="position-absolute start-0 top-0 z-index--1"
      />

      <img
        src="assets/images/shapes/curve-pattern2.png"
        alt=""
        className="position-absolute end-0 top-0 z-index--1"
      />

      <div className="container container-two">
        <div className="section-heading style-left style-white flx-between max-w-unset gap-4">
          <div>
            <h3 className="section-heading__title">
              Recommended Healthcare Centres
            </h3>

            <p className="section-heading__desc font-18">
              Discover trusted hospitals, clinics, and labs near you. Handpicked
              based on patient reviews, service quality, and reliability. Book
              confidently and get the care you deserve.
            </p>
          </div>

          <Link to="/centers" className="btn btn-main btn-lg pill fw-300">
            View All Healthcare Centres
          </Link>
        </div>

        <div className="selling-product-slider">
          <Slider {...settings}>
            <div className="product-item shadow-sm overlay-none">
              <div className="product-item__thumb d-flex max-h-unset">
                <Link to={`/centers?type=Hospital`} className="link w-100">
                  <img
                    src="assets/images/centers/hospital11.webp"
                    alt=""
                    className="cover-img"
                  />
                </Link>
              </div>

              <div className="product-item__content">
                <h6 className="product-item__title">
                  <Link to={`/centers?type=Hospital`} className="link">
                    Hospitals
                  </Link>
                </h6>

              </div>
            </div>

            <div className="product-item shadow-sm overlay-none">
              <div className="product-item__thumb d-flex max-h-unset">
                <Link
                  to={`/centers?type=General Clinic`}
                  className="link w-100"
                >
                  <img
                    src="assets/images/centers/lab1.webp"
                    alt=""
                      loading="lazy"
                    className="cover-img"
                  />
                </Link>
              </div>

              <div className="product-item__content">
                <h6 className="product-item__title">
                  <Link to={`/centers?type=General Clinic`} className="link">
                    General Clinic
                  </Link>
                </h6>
              </div>
            </div>

            <div className="product-item shadow-sm overlay-none">
              <div className="product-item__thumb d-flex max-h-unset">
                <Link
                  to={`/centers?type=Dental Care Centre`}
                  className="link w-100"
                >
                  <img
                    src="assets/images/centers/dental-care1.webp"
                    alt=""
                    className="cover-img"
                  />
                </Link>
              </div>

              <div className="product-item__content">
                <h6 className="product-item__title">
                  <Link
                    to={`/centers?type=Dental Care Centre`}
                    className="link"
                  >
                    Dental Care Centre
                  </Link>
                </h6>
              </div>
            </div>

            <div className="product-item shadow-sm overlay-none">
              <div className="product-item__thumb d-flex max-h-unset">
                <Link
                  to={`/centers?type=Labs and Diagnostic`}
                  className="link w-100"
                >
                  <img
                    src="assets/images/centers/lab1.webp"
                    alt=""
                      loading="lazy"
                    className="cover-img"
                  />
                </Link>
              </div>

              <div className="product-item__content">
                <h6 className="product-item__title">
                  <Link
                    to={`/centers?type=Labs and Diagnostic`}
                    className="link"
                  >
                    Labs and diagnostics
                  </Link>
                </h6>
              </div>
            </div>

            <div className="product-item shadow-sm overlay-none">
              <div className="product-item__thumb d-flex max-h-unset">
                <Link to={`/centers?type=AYUSH Centre`} className="link w-100">
                  <img
                    src="assets/images/centers/hospital21.webp"
                    alt=""
                    className="cover-img"
                  />
                </Link>
              </div>

              <div className="product-item__content">
                <h6 className="product-item__title">
                  <Link to={`/centers?type=AYUSH Centre`} className="link">
                    AYUSH Centre
                  </Link>
                </h6>
              </div>
            </div>

            <div className="product-item shadow-sm overlay-none">
              <div className="product-item__thumb d-flex max-h-unset">
                <Link
                  to={`/centers?type=Miscellaneous Healthcare Centre`}
                  className="link w-100"
                >
                  <img
                    src="assets/images/centers/Miscellaneous1.webp"
                    alt=""
                    className="cover-img"
                  />
                </Link>
              </div>

              <div className="product-item__content">
                <h6 className="product-item__title">
                  <Link
                    to={`/centers?type=Miscellaneous Healthcare Centre`}
                    className="link"
                  >
                    Miscellaneous Healthcare
                  </Link>
                </h6>
              </div>
            </div>
          </Slider>
        </div>
      </div>
    </section>
  );
};

export default SellingOne;
